<template>
  <Wrapper>
    <WrapperSubpages>
      <Grid>
        <Header>
          <SubPageNavigator
            v-bind="{
              home: true,
              isHeader: true,
            }"
          />
        </Header>
        <Nav />
      </Grid>
      <section>
        <Grid>
          <div class="grid-column forSubPages">
            <div class="title txt">
              <h2>Getting practical</h2>

              <p>
                Service design
                <br />Design management
                <br />Design operations
                <br />Design engineering
                <br />
                <br />
              </p>
            </div>
            <div class="contentForSubPages txt">
              <mq-layout mq="m">
                <img src="/static/img/getting-practical-01-m-633.svg" />
              </mq-layout>
              <mq-layout mq="s">
                <img src="/static/img/getting-practicalr-01-s-598.svg" />
              </mq-layout>
              <mq-layout :mq="['xs', '<xs']">
                <img src="/static/img/getting-practical-01-xs-275.svg" />
              </mq-layout>
              <p>
                Design is not a file created with your computer, neither a wall
                full of post-its. Its the experience you deliver, when all sums
                up in a public release. It mirrors the capabilities an
                organization has to form value for someone else. Even with
                ambition and resources, a potential breakthrough is hard to
                craft.
              </p>
