<script>
import Wrapper from "../Wrapper.vue";
import Grid from "../Grid.vue";
import Header from "../Header.vue";
import Nav from "../Nav.vue";
import WrapperSubpages from "./WrapperSubpages.vue";
import SubPageNavigator from "./SubPageNavigator.vue";
import Highlight from "./Highlight.vue";
import HighlightHook from "./HighlightHook.vue";
import AdvancedTable from "./AdvancedTable.vue";

export default {
  name: "ActLikeADesigner",
  components: {
    Wrapper,
    Grid,
    Header,
    WrapperSubpages,
    SubPageNavigator,
    Nav,
    Highlight,
    HighlightHook,
    AdvancedTable
  },
  data: function() {
    return {
      highlights: ["Innovation", "Qualification"]
    };
  }
};
</script>
<style scoped>
h3.specialSpacing {
  margin-top: calc(var(--LINE-HEIGHT) * 1);
}
</style>



